@media (min-width:1000px){
 #all-songs{
  grid-template-columns: 50% 50%;
  font-size: 28px;
 }
 #about{
    grid-template-columns: 50% 50%;
    .container-img-about{
     display: flex;
     padding: 150px;
    }
    .container-paragraph-about{
     padding: 100px;
     p{
      font-size: 20px;
     }
    }
 }
}
@media (max-width:1000px){
 #all-songs{
  grid-template-columns: auto;
  h4{
   font-size: 30px;
  }
 }
 #about{
  grid-template-columns: 100%;
    .container-img-about{
     display: none;
    }
    .container-paragraph-about{
     padding: 50px;
     p{
      font-size: 25px;
     }
    }
 }
}
@media (min-width:700px){
   
   #contact{
      grid-template-columns: 35vw 50vw;
   }
}

@media (max-width:700px){
   #contact{
      grid-template-columns: auto;
      #email-form-container{
         width: 80vw;
      }
   }
}

@media (max-width:550px){
 .navbar{
  .container-fluid{
   display: grid !important;
   grid-template-columns: auto auto;
   align-items:center !important;
   justify-content: space-between !important;
   .img-container{
      img{
         height: 50px!important;
         margin: 0;
      }
      
   }
   .links{
      display: none;
   }
   #hamburger-button{
      button{
        border: #fff 0px;
        box-shadow: none;
        display: flex;
        font-size: 30px;
      }
   }
  }
 }
   .links-hamburger{
      display: flex;
      background: #fff;
      z-index: 2;
      position: sticky;
      top: 66px;
      h4{
         &:hover{
            color: rgba($color: #000000, $alpha: 0.7);
         }
      }
   }
  #all-songs{
   padding-top: 120px;
  grid-template-columns: auto;
  h4{
   font-size: 20px;
  }
 }
 #about{
    padding-top: 100px;
 }
}

@media (min-width:550px){
   #all-songs{
      padding-top: 60px;
   }
   #about{
       padding-top: 100px;
   }
   .links-hamburger{
      display: none!important;
   }
   #hamburger-button{
      display: none;
   }
}